<template>
  <div>
    <h2>产品管理</h2>
    <a-row type="flex" style="margin-bottom: 20px;">
      <a-col style="width: 100%; display: flex;">
        <!-- <div style="width: 100%;">
          <a-input v-model="searchParams.keyword" style="max-width: 200px; margin-right: 12px;" />
          <a-button type="primary">搜索</a-button>
        </div> -->
        <a-button type="primary" @click="popNews">添加广告</a-button>
      </a-col>
    </a-row>
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="cover" slot-scope="text">
        <div
          class="bg_img"
          :style="`background-image: url(${imgPrefix}${text.cover_img}); width: 148px; height: 148px;`"
        />
      </template>
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="toEdit(text.id)">编辑</a-button>
        <a-button type="link" @click="toDelete(text.id)">删除</a-button>
      </template>
    </s-table>

    <a-modal
      v-model="newsVisible"
      :title="isEdit ? '编辑产品':'添加产品'"
      width="60%"
      @ok="handleSubmit"
      :okButtonProps="{
        props: {
          loading: submitLoading
        }
      }"
    >
      <a-spin :spinning="modalLoading">
        <a-form>
          <a-form-item label="广告标题">
            <a-input v-model="form.title" />
          </a-form-item>
          <a-form-item label="广告内容">
            <a-input v-model="form.content" />
          </a-form-item>
          <a-form-item label="广告封面">
            <a-upload
              name="file"
              list-type="picture-card"
              class="avatar-uploader"
              :show-upload-list="false"
              :action="`${uploadPrefix}/upload`"
              @change="hanldeCoverChange"
            >
              <div
                v-if="form.cover_img"
                class="bg_img cover_img"
                :style="`background-image: url(${imgPrefix}${form.cover_img});`"
              ></div>
              <div v-else>
                <a-icon :type="coverLoading ? 'loading' : 'plus'" />
                <div class="ant-upload-text">
                  Upload
                </div>
              </div>
            </a-upload>
          </a-form-item>
          <a-form-item label="关联产品">
            <a-select 
              v-model="form.product_id" 
              :options="product"
            />
          </a-form-item>
        </a-form>
      </a-spin>
    </a-modal>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
const getDefaultSearchParams = () => ({
  keyword: ''
})
const getFormDefaultParams = () => ({
  title: '',
  cover_img: '',
  content: '',
  product_id: '',
  shop_id: ''
})
export default {
  components: { STable },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '标题',
        dataIndex: 'title'
      },
      {
        title: '内容',
        dataIndex: 'content'
      },
      {
        title: '封面图',
        scopedSlots: { customRender: "cover"},
      },
      {
        title: '操作',
        scopedSlots: { customRender: "action"},
      }
    ]
    return {
      columns,
      searchParams: getDefaultSearchParams(),
      newsVisible: false,
      isEdit: false,
      submitLoading: false,
      modalLoading: false,
      form: getFormDefaultParams(),
      proxy: process.env.VUE_APP_API_URL,
      prefix: process.env.VUE_APP_PREFIX,
      imgPrefix: process.env.VUE_APP_IMG_PREFIX,
      uploadPrefix: process.env.VUE_APP_UPLOAD_PREFIX,
      coverLoading: false,
      loadData: paramters => {
        return this.getNewsList(
          Object.assign({}, paramters, {shop_id: sessionStorage.getItem('shop_id') ? Number(sessionStorage.getItem('shop_id')): undefined})
        )
      }
    }
  },
  computed: {
    ...mapState('advertisement', ['detail']),
    ...mapState('product', {
      productList: 'list'
    }),
    product() {
      if (this.productList.length === 0) return []
      return this.productList.map(item => {
        return {
          label: item.name,
          value: item.id
        }
      })
    }
  },
  watch: {
    newsVisible() {
      if (!this.newsVisible) {
        this.form = getFormDefaultParams()
      }
    }
  },
  methods: {
    ...mapActions('advertisement', {
      getNewsList: 'getList',
      addNews: 'add',
      updateNews: 'update',
      getNewsDetail: 'getDetail',
      deleteNews: 'delete'
    }),
    ...mapActions('product', {
      getProductList: 'getList'
    }),
    toEdit(id) {
      this.isEdit = true
      this.modalLoading = true
      this.newsVisible = true
      this.getNewsDetail(id).then(res => {
        this.form = Object.assign({}, this.form, res.data)
        this.form.product_id = res.data.product.id
      }).finally(() => {
        this.modalLoading = false
      })
    },
    toDelete(id) {
      const _this = this
      this.$confirm({
        content: '你确定要删除这个产品？',
        okText: '确认',
        cancelText: '取消',
        onOk() {
          _this.deleteNews(id).then(res => {
            _this.$refs.table.refresh(true)
          })
        }
      });
    },
    popNews() {
      this.isEdit = false
      this.form = getFormDefaultParams()
      this.newsVisible = true
    },
    hanldeCoverChange(e) {
      const file = e.file
      if (file.status === 'uploading') {
        this.form.cover_img = ''
        this.coverLoading = true
      }
      if (file.status === 'done') {
        this.form.cover_img = file.response.data
        this.coverLoading = false
      }
    },
    handleSubmit() {
      if (this.modalLoading) {
        this.newsVisible = false
        return ;
      }
      this.form.shop_id = Number(sessionStorage.getItem('shop_id'))
      if (!this.form.shop_id) {
        this.$message.error('商户id丢失')
        return ;
      }
      this.submitLoading = true
      if (this.isEdit) {
        this.updateNews({
          id: this.form.id,
          data: this.form
        }).then(() => {
          this.$message.success('修改成功')
          this.$refs.table.refresh(true)
          this.newsVisible = false
        }).finally(() => { 
          this.submitLoading = false 
          })
      } else {
        this.addNews(this.form).then(() => {
          this.$message.success('添加成功')
          this.$refs.table.refresh(true)
          this.newsVisible = false
        }).finally(() => { 
          this.submitLoading = false 
          })
      }
    }
  },
  created() {
    this.getProductList({limit: -1})
  }
}
</script>
<style lang="scss" scoped>
/deep/.ant-modal {
  /deep/.ant-form-item {
    margin-bottom: 0;
  }
}
</style>